/*
** Settings - Layout
** ----------------------------------------------------------------------------- */

// Container
$container-width: var(--chipmunk-container-width, 110rem) !default;
$container-gutter: var(--chipmunk-container-gutter, clamp(2.4rem, 5vw, 7.2rem)) !default;

// Columns
$column-gutter: var(--chipmunk-column-gutter, 1.75em) !default;

// Content
$content-size: var(--chipmunk-content-font-size, 1.25em) !default;
$content-width: var(--chipmunk-content-width, 8) !default;

// Popup
$popup-width: var(--chipmunk-popup-width, 80rem) !default;
