/*
** Component - Tile
** ----------------------------------------------------------------------------- */

$grid-columns: (
  sm: 1,
  md: 2,
  lg: 3,
);

.c-tile {
  display: flex;
  position: relative;
  transition: opacity $transition-duration;

  &[href]:hover {
    opacity: 0.9;
  }

  &--wide {
    grid-column: 1 / -1;
  }
}

.c-tile__list {
  display: grid;
  gap: $column-gutter;

  // Grid variants
  @each $breakpoint, $columns in $grid-columns {
    @include bp($breakpoint) {
      grid-template-columns: repeat($columns, 1fr);
    }
  }

  // Gap variants
  &--separated {
    row-gap: calc(#{$column-gutter} * 2);
  }

  // Alignment variants
  &--start {
    align-items: start;
  }

  &--center {
    align-items: center;
  }

  &--end {
    align-items: end;
  }

  &.flickity-enabled {
    display: block;
    width: calc(100% + #{$column-gutter});
    margin: calc(#{$column-gutter} * -0.5);

    .c-tile {
      width: 100%;
      min-height: 100%;
      padding: calc(#{$column-gutter} * 0.5);

      &:not(.c-tile--wide) {
        @include bp(md) {
          width: 50%;
        }

        @include bp(lg) {
          width: 33.333%;
        }
      }
    }
  }

  .flickity-viewport {
    transition: height 0s;
  }
}

.c-tile__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: spacer-fixed(1);
  width: 100%;
  overflow: hidden;

  .c-tile--card &,
  .c-tile--wide & {
    background-color: $color-section;
    box-shadow: 0 1px 2px 1px rgba($color-black, 0.1);
  }

  .c-tile--wide & {
    @include bp(md) {
      gap: spacer-fixed(1.5);
      flex-direction: row;
    }
  }
}

.c-tile__image {
  position: relative;
  background-color: $color-gray-light;
  aspect-ratio: 4 / 3;
  overflow: hidden;
  transition: opacity $transition-duration;

  > img,
  > svg,
  > video,
  > iframe {
    position: absolute;
    inset: 0;
    height: 100%;
    width: 100%;
    object-fit: cover;
    box-shadow: -2px 0 10px 0 rgba($color-black, 0.25);
    transition: transform $transition-duration ease-in-out 0.05s;

    &:nth-of-type(2) {
      transform: translateX(25%);

      [href]:hover & {
        transform: translateX(33.3%);
      }
    }

    &:nth-of-type(3) {
      transform: translateX(50%);

      [href]:hover & {
        transform: translateX(66.6%);
      }
    }
  }

  &[href]:hover {
    opacity: 0.8;
  }

  .c-tile--wide & {
    @include bp(md) {
      width: 40%;
    }

    @include bp(lg) {
      width: 25%;
    }
  }
}

.c-tile__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: spacer-fixed(1);
  flex: 1;
  padding: spacer-fixed(1.5);
  color: $color-black;

  &--dimmed {
    background-color: rgba($color-black, 0.75);
    color: $color-white;
  }

  &--primary {
    background-color: $color-primary;
    color: $color-white;
  }

  .c-tile--tile & {
    position: absolute;
    inset: 0;
    z-index: $layer-base;
  }

  .c-tile--card &:not(:first-child) {
    padding: spacer-fixed(1);
    padding-top: 0;
  }

  .c-tile--blank & {
    padding: 0;
  }

  .c-tile--wide &:not(:first-child) {
    @include bp(md) {
      padding: spacer-fixed(1.5);
      padding-left: 0;
    }
  }
}

.c-tile__info {
  display: flex;
  flex-direction: column;
  align-items: start;
  flex: 1;
}

.c-tile__head {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: spacer-fixed(0.75);
  width: 100%;
}

.c-tile__title {
  flex: 1;
  color: inherit;

  a {
    display: inline-block;

    &:hover {
      opacity: 0.8;
    }
  }
}

.c-tile__icon {
  margin: -0.15em;
  padding: 0.15em;
  opacity: 0.5;
  cursor: pointer;
  vertical-align: middle;
  transition: opacity $transition-duration;
  transform: translateY(0.5em);

  .u-icon {
    display: block;
  }

  &:hover {
    opacity: 1;
  }
}

.c-tile__copy {
  @include line-clamp(2);

  width: 100%;
  font-size: 0.875em;
  opacity: 0.85;

  .c-tile--card & {
    opacity: 1;
  }
}

.c-tile__stats {
  margin-top: auto;

  .c-tile--card &,
  .c-tile--blank &,
  .c-tile--wide & {
    color: $color-gray;
  }
}

.c-tile__button {
  align-self: start;

  [href]:hover &,
  &[href]:hover {
    background-color: $color-primary;
    border-color: transparent;
    color: $color-white;
  }
}

.c-tile__status {
  position: absolute;
  bottom: 2rem;
  left: 2rem;
  z-index: $layer-base;
  padding: 0.5em 0.75em;
  background-color: $color-gray;
  border-radius: $button-border-radius;
  color: $color-white;
  font-size: 0.75em;
  line-height: 1;

  &--publish {
    background-color: $color-status-success;
  }

  &--trash {
    background-color: $color-status-error;
  }
}
