/*
** Settings - Typography
** ----------------------------------------------------------------------------- */

// Fonts
$font-system: -apple-system, BlinkMacSystemFont, 'Segoe UI', roboto, oxygen, ubuntu, cantarell, 'Open Sans',
  'Helvetica Neue', sans-serif !default;
$font-mono: menlo, consolas, monaco, monospace !default;

// Typographic scale
$font-heading-scale: 1.25 !default;

// Golden ratio
$font-line-height: 1.618 !default;

// Customizable type
$body-type: (
  font-family: var(--chipmunk-body-font-family, #{$font-system}),
  font-size: var(--chipmunk-body-font-size, 1.6rem),
  font-weight: var(--chipmunk-body-font-weight, 400),
  font-style: var(--chipmunk-body-font-style, normal),
  letter-spacing: var(--chipmunk-body-letter-spacing, 0),
  line-height: var(--chipmunk-body-line-height, 1.6),
  text-transform: var(--chipmunk-body-text-transform, none),
) !default;

$heading-type: (
  font-family: var(--chipmunk-heading-font-family, #{$font-system}),
  font-size: var(--chipmunk-heading-font-size, 2.2rem),
  font-weight: var(--chipmunk-heading-font-weight, 700),
  font-style: var(--chipmunk-heading-font-style, normal),
  letter-spacing: var(--chipmunk-heading-letter-spacing, 0),
  line-height: var(--chipmunk-heading-line-height, 1.4),
  text-transform: var(--chipmunk-heading-text-transform, none),
) !default;
