/*
** Component - Comment
** ----------------------------------------------------------------------------- */

$comment-avatar-size: 3.2rem;

.c-comment,
.c-comment .children {
  @include spacer-clamped(2);
}

.c-comment__body {
  display: flex;
  flex-wrap: nowrap;

  + .children {
    margin-left: calc(#{$comment-avatar-size} + #{spacer-fixed(1)});
  }
}

.c-comment__image {
  width: $comment-avatar-size;
  margin-right: spacer-fixed(1);

  img {
    border-radius: $button-border-radius;
  }
}

.c-comment__info {
  flex: 1;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: start;
}

.c-comment__title {
  display: inline-block;
  margin-right: 0.5em;
  vertical-align: middle;
}

.c-comment__date {
  display: block;
  color: $color-gray;
  font-size: 0.9em;
  transition: color $transition-duration;

  @include bp(lg) {
    color: $color-gray-light;

    .c-comment__body:hover & {
      color: $color-gray;
    }
  }
}

.c-comment__content {
  @include spacer-relative(0.125);

  @extend %copy;

  width: 100%;
  font-size: 1.25em;
}

.c-comment__reply {
  @include spacer-relative(0.5);

  a {
    display: inline-flex;
    align-items: center;
    gap: spacer-clamped(0.5);
    font-size: 0.875em;

    &:hover {
      opacity: 0.8;
    }
  }
}

.c-comment__note {
  margin-top: 1em;
  font-style: italic;
}

.c-comment-respond {
  @include spacer-clamped(4);

  p {
    grid-column: 1 / -1;
    text-align: left;

    a {
      &:hover {
        color: $color-black;
      }
    }
  }

  label {
    cursor: pointer;
  }

  [type='checkbox'] {
    margin-right: 0.5em;
  }
}
