/*
** Component - Page foot
** ----------------------------------------------------------------------------- */

.c-page-foot {
  a,
  button {
    &:hover {
      color: $color-black;
    }
  }
}

.c-page-foot__inner {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: space-between;
  gap: $column-gutter;

  @include bp(md) {
    flex-direction: row;
    text-align: left;
  }
}

.c-page-foot__column {
  display: flex;
  flex-direction: column;
  gap: spacer-clamped(1);
  width: 100%;

  &:first-child {
    @include bp(md) {
      width: calc(1 / 2 * 100%);
      margin-right: auto;
    }
  }

  &:not(:first-child) {
    @include bp(md) {
      width: calc(1 / 3 * 100%);
    }

    @include bp(lg) {
      width: calc(1 / 6 * 100%);
    }
  }
}

.c-page-foot__description {
  @extend %copy;

  color: $color-gray;
}

.c-page-foot__copy,
.c-page-foot__credits {
  color: $color-gray;
  text-align: center;

  @include bp(md) {
    text-align: left;
  }
}

.c-page-foot__credits {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: spacer-fixed(0.5);
  white-space: nowrap;

  svg {
    width: auto;
    height: calc(#{map-get($body-type, line-height)} * 1em);
    fill: currentColor;
  }
}
