/*
** Component - Tag
** ----------------------------------------------------------------------------- */

.c-tag {
  &:not(:last-child)::after {
    content: ',';
  }
}

.c-tag__list {
  display: flex;
  align-items: center;
  gap: spacer-fixed(0.25);
  color: $color-gray;
  font-size: 0.875em;

  .u-icon {
    font-size: 1.25em;
  }

  a:hover {
    color: $color-black;
    opacity: 0.8;
  }
}
