/*
** Placeholders - Caption
** ----------------------------------------------------------------------------- */

%caption {
  margin: spacer-clamped(1) 0 0;
  color: $color-gray;
  font-size: 0.875em;
  word-wrap: break-word;
}

%caption-quote {
  @include spacer-relative(1);

  display: inline-block;

  &::before {
    content: '– ';
  }
}
