/*
** Placeholders - Heading
** ----------------------------------------------------------------------------- */

@use 'sass:math';

%heading {
  @include typography-properties($heading-type, (font-size, line-height));

  line-height: calc(1.5rem + 1.5ex);
}

@for $i from 1 through 6 {
  %heading-h#{$i} {
    font-size: min(10vw, calc(1em * #{math.pow($font-heading-scale, 6 - $i)}));
  }
}
