/*
** Settings - Colors
** ----------------------------------------------------------------------------- */

// Default colors
$color-accent: #f38181 !default;
$color-black: #1d1d1d !default;
$color-gray-dark: #666 !default;
$color-gray: #808080 !default;
$color-gray-light: #cdcdcd !default;
$color-gray-lighter: #e6e6e6 !default;
$color-gray-lightest: #ededed !default;
$color-white: #fafafa !default;

// Status colors
$color-status-success: #4f8a10 !default;
$color-status-warning: #9f6000 !default;
$color-status-info: #369bf8 !default;
$color-status-error: #f74444 !default;

// Customizable colors
$color-primary: var(--chipmunk-color-primary, #{$color-accent}) !default;
$color-link: var(--chipmunk-color-link, #{$color-accent}) !default;
$color-background: var(--chipmunk-color-background, #{$color-gray-lightest}) !default;
$color-section: var(--chipmunk-color-section, #{$color-white}) !default;
