/*
** Component - Stats
** ----------------------------------------------------------------------------- */

.c-stats {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: spacer-fixed(0.75) spacer-fixed(1);
  font-size: 0.8125em;
  line-height: 1.4;
}

.c-stats__item,
.c-stats__button > span {
  display: flex;
  align-items: center;
  gap: 0.5em;
}

.c-stats__item {
  a:hover {
    opacity: 0.8;
  }

  .u-icon {
    font-size: 1.25em;
  }
}

.c-stats__button {
  @extend %link-transition;
  @extend %button;
  @extend %button-rounded;

  @include loader;

  padding-left: 0.75em;
  padding-right: 0.75em;
  border-color: $color-gray;
  font-size: inherit;
  backface-visibility: hidden;

  &:not(.is-active):hover {
    color: $color-primary;
    border-color: $color-primary;
  }

  &.is-active {
    background-color: $color-primary;
    border-color: transparent;
    color: $color-white;
  }

  .u-icon {
    display: inline-block;
    backface-visibility: hidden;
  }
}

.c-stats__tag {
  & + &::before {
    content: ', ';
    color: $color-gray;
  }

  &--dimmed {
    color: lighten($color-gray, 20%);

    &::before {
      color: lighten($color-gray, 20%);
    }
  }
}
