/*
** Blocks / Common
** ----------------------------------------------------------------------------- */

.wp-block-buttons,
.wp-block-code,
.wp-block-table,
.wp-block-latest-posts .wp-block-subhead,
.wp-block-categories,
.wp-block-verse,
.wp-block-preformatted {
  &[class] {
    @include spacer-clamped(2.5, 'top');

    margin-top: 0;
    margin-bottom: 0;
  }
}

.wp-block-image,
.wp-block-cover,
.wp-block-gallery,
.wp-block-embed,
.wp-block-quote,
.wp-block-pullquote,
.wp-block-columns,
.wp-block-text-columns,
.wp-block-separator {
  &[class] {
    @include spacer-clamped(5, 'top');
    @include spacer-clamped(5, 'bottom');

    margin-top: 0;
    margin-bottom: 0;
  }
}
