/*
** Component - Resource
** ----------------------------------------------------------------------------- */

.c-resource {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: spacer-fixed(2.5) spacer-fixed(2.5);
}

.c-resource__content,
.c-resource__media {
  flex-basis: 100%;

  @include bp(lg) {
    flex-basis: calc(50% - #{spacer-fixed(1.25)});
    order: 3;

    &--full {
      flex-basis: 100%;
    }
  }
}

.c-resource__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-self: stretch;
  gap: spacer-fixed(1.25);
}

.c-resource__media {
  display: block;
  aspect-ratio: 4 / 3;

  > img,
  > svg,
  > video,
  > iframe {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }

  &[href] {
    &:hover {
      opacity: 0.8;
    }
  }
}

.c-resource__head {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  gap: spacer-clamped(2.5) spacer-fixed(1.25);
  width: 100%;
}

.c-resource__info {
  display: flex;
  flex-direction: column;
  gap: spacer-fixed(1.25);
  margin-bottom: auto;
}

.c-resource__title {
  a:hover {
    color: $color-gray-dark;
  }
}

.c-resource__description {
  @extend %copy;
  @extend %copy-type;

  a {
    color: $color-primary;

    &:hover {
      color: $color-black;
    }
  }

  img,
  .wp-caption {
    max-width: 100%;
    height: auto !important;
  }

  p {
    @include spacer-relative(0.5);
  }
}

.c-resource__stats {
  color: $color-gray;

  a:hover {
    color: $color-black;
  }
}

.c-resource__actions {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: spacer-clamped(0.5) spacer-clamped(1);

  @include bp(lg) {
    justify-content: start;
  }
}
