/*
** Settings - Miscellaneous
** ----------------------------------------------------------------------------- */

// Responsive display values
$display-values: (inline, inline-block, block, flex) !default;

// Responsive breakpoints
$breakpoints: (
  sm: 0,
  md: 680px,
  lg: 1024px,
) !default;

// Transitions
$transition-duration: var(--chipmunk-transition-duration, 0.25s) !default;

// Spacers
$spacer: var(--chipmunk-spacer, 1.25em) !default;

// Borders
$border-opacity: var(--chipmunk-border-opacity, 0.075) !default;
$border-radius: var(--chipmunk-border-radius, 0.5rem) !default;

// Logo
$logo-height: var(--chipmunk-logo-height, 4rem) !default;
