/*
** Layout - Header
** ----------------------------------------------------------------------------- */

.l-header {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: $spacer;
}

.l-header__copy {
  color: $color-gray;

  a:hover {
    color: $color-black;
  }

  @include bp(lg) {
    width: 100%;
    padding-right: calc(33.3% - #{$column-gutter} * 0.25);
  }
}
