/*
** Layout - Section
** ----------------------------------------------------------------------------- */

.l-section {
  padding-top: spacer-clamped(5);
  padding-bottom: spacer-clamped(5);

  &--compact {
    padding-top: spacer-clamped(2.5);
    padding-bottom: spacer-clamped(2.5);
  }

  &--double {
    padding-top: spacer-clamped(10);
    padding-bottom: spacer-clamped(10);
  }

  &--theme-light {
    @extend %bordered-box;

    background-color: $color-section;
  }

  &--theme-primary {
    background-color: $color-primary;
    color: $color-white;
  }

  &--intro + &:not([class*='theme']) {
    padding-top: 0;
  }

  &:not([class*='theme']):not([class*='intro']) + &:not([class*='theme']) {
    @extend %bordered-box;
  }
}
