/*
** Component - Entry
** ----------------------------------------------------------------------------- */

.c-entry {
  display: flex;
  flex-direction: column;
  gap: spacer-clamped(2);
}

.c-entry__tile {
  grid-column: 1 / -1;
}

.c-entry__hero {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: end;
  height: calc(50vh + var(--header-height, 0px));
  min-height: calc(36rem + var(--header-height, 0px));
  max-height: calc(75rem + var(--header-height, 0px));
  margin-top: calc(var(--header-height, 0px) * -1);
  padding-top: var(--header-height, 0);
  background-color: rgba($color-black, 0.9);
  overflow: hidden;

  @include bp(md) {
    height: calc(40vh + var(--header-height, 0px));
    min-height: calc(55vw + var(--header-height, 0px));
  }

  @include bp(lg) {
    height: auto;
    min-height: calc(45rem + var(--header-height, 0px));

    @media (min-height: 800px) {
      height: calc(60vh + var(--header-height, 0px));
    }
  }

  .c-entry__title {
    color: inherit;
  }
}

.c-entry__background {
  @include cover(0.33, $color-black);

  position: absolute;
  inset: 0;
  height: 100%;
  width: 100%;
  background-color: transparent;
  pointer-events: none;
  user-select: none;
  transition: opacity $transition-duration;
  aspect-ratio: 16 / 9;

  > img,
  > svg,
  > video {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }
}

.c-entry__details {
  position: relative;
  z-index: $layer-base;
  color: $color-white;
}

.c-entry__image {
  @include spacer-clamped(3, 'bottom');

  display: block;

  &[href]:hover {
    opacity: 0.8;
  }

  img {
    width: 100%;
    height: auto;
  }
}

.c-entry__head {
  &:only-child,
  &:not(:last-child) {
    margin-bottom: spacer-clamped(3);
  }
}

.c-entry__title {
  @include spacer-clamped(1.5);

  a {
    display: inline-block;

    &:hover {
      color: $color-gray-dark;
    }
  }

  [href] & {
    color: $color-black;
  }
}

.c-entry__meta {
  @include spacer-clamped(1.5);

  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: spacer-fixed(0.75) spacer-fixed(1);
  opacity: 0.8;
}

.c-entry__stats {
  display: contents !important;
}

.c-entry__content {
  &:empty {
    display: none;
  }
}

.c-entry__footer {
  display: flex;
  flex-direction: column;

  @include bp(md) {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .c-menu-socials {
    &:not(:first-child) {
      margin-top: $spacer;

      @include bp(md) {
        margin-top: 0;
      }
    }
  }
}

.c-entry__author {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  font-size: 0.875em;
  line-height: 1.2;

  img {
    margin-right: spacer-clamped(0.75);
    border-radius: 50%;
  }

  strong {
    display: inline-block;
    font-weight: bold;
  }
}

.c-entry__button {
  display: inline-block;
  margin-top: spacer-clamped(3);

  .c-entry[href]:hover & {
    background-color: $color-primary;
    border-color: transparent;
    color: $color-white;
  }
}
