/*
** Component - Popup
** ----------------------------------------------------------------------------- */

.c-popup {
  position: fixed;
  inset: 0;
  z-index: $layer-popup;
  overflow: hidden;
  pointer-events: none;
  transform: translateX(-300%);
  transition: transform 0s $transition-duration;

  .has-popup-open & {
    transform: translateX(0);
    pointer-events: all;
    transition: none;
  }
}

.c-popup__close {
  position: absolute;
  top: spacer-clamped(2);
  right: spacer-clamped(2);
  z-index: $layer-utils;
  color: $color-black;
  line-height: 1;
  transition-property: opacity, color;
  transition-duration: calc($transition-duration * 1.5);

  &:hover {
    color: $color-primary;
  }
}

.c-popup__content {
  position: absolute;
  top: 50%;
  left: 50%;
  width: calc(100% - (#{$container-gutter} * 2));
  max-height: calc(100% - (#{$column-gutter} * 2));
  max-width: min(#{$popup-width}, calc(100vw - var(--scrollbar-width, 0px) - #{$container-gutter} * 2));
  margin-top: 5%;
  padding: spacer-clamped(4);
  background-color: $color-section;
  color: $color-black;
  opacity: 0;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  transform: translate3d(-50%, -50%, 0);
  transition: opacity calc($transition-duration * 1.5), margin calc($transition-duration * 1.5);

  .has-popup-open & {
    opacity: 1;
    margin-top: 0;
  }

  > img {
    width: auto;
    height: auto;
    max-height: 100%;
    margin: auto;
  }
}
