/*
** Blocks / Pullquote
** ----------------------------------------------------------------------------- */

.wp-block-pullquote {
  padding-top: spacer-clamped(2);
  padding-bottom: spacer-clamped(2);
  border-top: 4px solid $color-gray;
  border-bottom: 4px solid $color-gray;
  text-align: center;

  p {
    font-size: 1.2em;
  }

  cite,
  footer {
    @extend %caption;
    @extend %caption-quote;
  }
}
