/*
** Component - Heading
** ----------------------------------------------------------------------------- */

.c-heading {
  @extend %heading;

  @for $i from 1 through 6 {
    &--h#{$i} {
      @extend %heading-h#{$i} !optional;
    }
  }

  small {
    color: $color-gray;
  }
}

.c-heading__link {
  display: inline-block;
  cursor: pointer;
  opacity: 0.5;
  transition: opacity $transition-duration;

  &:hover,
  &:focus {
    opacity: 0.75;
  }

  &.is-active {
    opacity: 1;
  }
}
