/*
** Component - Page head
** ----------------------------------------------------------------------------- */

.c-page-head {
  @extend %bordered-box;

  z-index: $layer-head;
  background-color: $color-section;

  &[data-sticky] {
    inset: 0 0 auto;

    .admin-bar & {
      @media (min-width: 600px) {
        top: 4.6rem;
      }

      @media (min-width: 782px) {
        top: 3.2rem;
      }
    }
  }
}

.c-page-head__inner {
  padding-top: spacer-clamped(2);
  padding-bottom: spacer-clamped(2);
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: spacer-clamped(2);
  min-height: $logo-height;
  transition-property: padding;
  transition-duration: $transition-duration;

  .is-condensed & {
    padding-top: spacer-clamped(1);
    padding-bottom: spacer-clamped(1);
  }
}

.c-page-head__logo {
  color: $color-primary;
  font-weight: bold;

  a {
    display: block;

    &:hover {
      color: $color-black;
    }
  }

  img {
    display: inline-block;
    width: auto;
    height: $logo-height;
    vertical-align: middle;
  }
}

.c-page-head__cta {
  display: flex;
  align-items: center;
  justify-content: end;
  gap: spacer-fixed(0.75);
  margin-left: auto;
}

.c-page-head__search {
  position: absolute;
  inset: -100% 0 100%;
  z-index: $layer-base;
  display: flex;
  align-items: center;
  gap: $spacer;
  background-color: $color-section;
  opacity: 0;
  pointer-events: none;
  transition-property: bottom, top, opacity;
  transition-duration: calc($transition-duration * 1.5);

  .has-search-open & {
    top: 0;
    bottom: 0;
    opacity: 1;
    pointer-events: all;
  }
}

.c-page-head__placeholder {
  height: var(--header-height, 0);
}
