/*
** Component - Menu socials
** ----------------------------------------------------------------------------- */

.c-menu-socials {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: spacer-clamped(0.5);
}

.c-menu-socials__title {
  @include spacer-clamped(0.5, 'right');
}

.c-menu-socials__list {
  display: contents;
}

.c-menu-socials__link {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2em;
  width: 2em;
  border: $button-border-width solid $color-gray-light;
  border-radius: $button-border-radius;
  color: $color-gray-dark;
  font-size: $button-font-size;
  line-height: 1;
  text-align: center;

  &:hover {
    border-color: $color-primary;
    color: $color-primary;
    opacity: 0.75;
    transform: scale(1.05);
  }
}
